//IPAD PRO RESOLUTION
.score-container {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));

  h1 {
    color: #f41111;
    font-size: 96px;
    text-align: center;
    margin-top: 10%;
    grid-column: 2;
  }

  section {
    grid-row-start: 2;
    grid-column: 1 / span 3;
    box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.4);
    margin: 10% 3%;
    list-style-position: inside;
    padding: 20px;
    background-color: white;
  }

  li {
    font-size: 64px;
    padding: 40px 10px;
    color: #0d50b5;
  }

  .button-highScore {
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.4);
    border: none;
    padding: 25px;
    text-align: center;
    font-size: 48px;
    color: #0d50b5;
    background: white;
    text-decoration: none;
    border-radius: 8px;
    cursor: pointer;
    margin: 3% 7% 0% 7%;


    &.button-highScore--play {
      grid-row-start: 3;
      grid-column: 1 / span 3;
    }

    &.button-highScore--prev {
      grid-row-start: 4;
      grid-column: 1 / span 3;
    }
  }


  //RESPONSIVE RESOLUTIONS
  //IPHONE XR
  @media screen and (max-width: 500px) {
    h1 {
      font-size: 36px;
    }
    section {
      box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.4);
      padding: 10px;
    }
    li {
      font-size: 24px;
      padding: 15px 5px;
    }
    .button-highScore {
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
      padding: 15px;
      font-size: 24px;
      margin: 5% 10% 3% 10%;
    }
  }

  //BETWEEN TABLET AND LAPTOP
  @media screen and (min-width: 1150px) {
    h1 {
      margin-top: 5%;
    }
    section {
      margin: 5%;
    }
    li {
      padding: 10px;
    }
    .button-highScore {
      margin: 3% 15% 0% 15%;
      font-size: 36px;
      padding: 10px;
      padding: 20px;
      font-size: 24px;
      border-radius: 8px;

      &.button-highScore--play {
        grid-column: 1;
        justify-self: start;
        padding: 20px 100px;
        margin: 10% 30%;
      }

      &.button-highScore--prev {
        grid-row-start: 3;
        grid-column: 3;
        justify-self: end;
        padding: 20px 100px;
        margin: 10% 30%;
      }
    }
  }

  //LAPTOP
  @media screen and (min-width: 1530px) {
    h1 {
      font-size: 48px;
      margin-top: 5%;
    }
    section {
      grid-row-start: 2;
      margin: 3% 10%;
    }
    li {
      font-size: 36px;
      padding: 10px;
    }
  }

  //LAPTOP
  @media screen and (min-width: 1700px) {
    h1 {
      font-size: 64px;
      margin-top: 20%;
    }
    section {
      grid-row-start: 2;
      margin: 5% 20%;
    }
    li {
      font-size: 48px;
      padding: 30px;
    }
    .button-highScore {
      padding: 25px;
      font-size: 36px;
      border-radius: 8px;

      &.button-highScore--play {
        grid-column: 1;
        justify-self: start;
        padding: 30px 110px;
        margin: 10% 58%;
      }

      &.button-highScore--prev {
        grid-row-start: 3;
        grid-column: 3;
        justify-self: end;
        padding: 30px 110px;
        margin: 10% 58%;
      }
    }
  }

  body {
    background-image: url("../../Assets/background.jpg");
  }

  .grid-container {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}